body {
  font-family: "Raleway", sans-serif;
  font-size: 22px;
  background-color: #231f20;
  color: #e59500;
  margin: 0px;
}

*,
::after,
::before {
  box-sizing: border-box;
}

.container {
  padding-top: 50px;
}

h6 {
  color: #e0ddcf;
  font-size: 0.5666666667em;
  font-family: "Questrial", sans-serif;
}

h5 {
  color: #e0ddcf;
  font-size: 1.1333333333em;
  font-family: "Questrial", sans-serif;
}

h4 {
  color: #e0ddcf;
  font-size: 1.7em;
  font-family: "Questrial", sans-serif;
}

h3 {
  color: #e0ddcf;
  font-size: 2.2666666667em;
  font-family: "Questrial", sans-serif;
}

h2 {
  color: #e0ddcf;
  font-size: 2.8333333333em;
  font-family: "Questrial", sans-serif;
}

h1 {
  color: #e0ddcf;
  font-size: 3.4em;
  font-family: "Questrial", sans-serif;
}

nav {
  position: fixed;
  background-color: #e59500;
  width: 100%;
  height: 55px;
  display: flex;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}

nav h1 {
  margin-top: 0.5em;
  margin-left: 10px;
  font-size: 1.3em;
  color: #231f20;
  flex-grow: 3;
  flex-basis: auto;
}

.language-select {
  color: #e0ddcf;
  flex-shrink: 1;
  width: 5em;
  padding: 0.5em;
}

.language-select:hover {
  cursor: pointer;
}

.language-select ul {
  color: #231f20;
  font-weight: bold;
  list-style: none;
  margin: 0;
  padding: 0;
}

.language-select ul li {
  position: relative;
}

.language-select ul ul {
  padding-top: 0.8em;
}

.language-select ul ul li {
  color: #e0ddcf;
  padding: 0.5em;
  width: 5em;
  background-color: #534b52;
}

a {
  color: #e0ddcf;
  text-decoration: none;
}

img {
  max-width: 100%;
}

small {
  color: #534b52;
}

.form-field {
  padding-top: 1em;
}

.form-field label {
  width: 100%;
  display: inline-block;
  color: #e0ddcf;
}

.form-field input {
  width: 100%;
  padding: 0.8em;
  font-size: 1em;
}

.form-field .error {
  color: #840032;
  background-color: #e0ddcf;
  padding: 0.3em;
}

button {
  padding: 10px;
  border: 0px;
  font-size: 1.2em;
}

button:hover {
  cursor: pointer;
  text-decoration: underline;
}

button.small {
  font-size: 0.5em;
}

button.default {
  background-color: #e59500;
}

button.abort {
  color: #840032;
  background-color: #e0ddcf;
}

